.navbar {
  position: relative;
  display: flex;
  align-items: center;
  background: #7bc300;
  padding: 10px 20px;
}
.navbar a {
  color: white;
  text-transform: uppercase;
}
.letter-line {
  display: flex;
  flex-wrap: wrap;
}
.letter-frame {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border: 1px solid lightgray;
  overflow: hidden;
  min-height: 130px;
  transition: all 0.3s ease-in-out;
  flex: 1 0 100px;
  margin: 5px;
}
.letter-frame img {
  width: 100%;
  height: 70px;
  object-fit: contain;
}
.letter-frame span {
  font-size: 30px;
  text-transform: uppercase;
}
.letter-frame .btn-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
}
.letter-frame:hover {
  box-shadow: 0 0 10px #00000078;
}
.letter-options {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  border-bottom: 1px solid lightgray;
  margin-bottom: 20px;
  padding: 10px;
}
.letter-options .words-frame {
  display: flex;
}
.letter-options p {
  font-size: 24px;
  margin-bottom: 0;
}
.letter-options-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 4px;
  left: 0;
  margin-bottom: 20px;
  z-index: 9999;
  background: white;
  box-shadow: 0 3px 7px 0 #0000006b;
}
.letter-empty {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-bottom: 1px solid lightgray;
  margin-bottom: 20px;
  min-height: 60px;
}
.letter-empty p {
  font-size: 24px;
  margin-bottom: 0;
  margin-left: 15px;
}
.words {
  text-transform: uppercase;
  min-width: 15px;
  color: darkgray;
  margin-bottom: 0;
}
.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
  padding: 10px 0;
  margin-left: -200px;
  flex: 1 0 200px;
  width: 200px;
  height: calc(100vh - 50px);
  background: white;
  border-right: 1px solid #7bc300;
  transition: margin-left 0.3s ease-in-out;
}
.sidebar .list-group-item {
  font-size: 18px;
  text-transform: capitalize;
}
.sidebar .list-group-item a {
  color: darkgray;
}
.sidebar.opened {
  margin-left: 0;
}
.toggleButton {
  position: fixed;
  z-index: 9999;
  top: 5px;
  left: 160px;
  color: white;
}
.toggleButton svg {
  width: 35px;
  height: 35px;
}
.main-layout {
  display: flex;
}
.main-frame {
  position: relative;
  width: 100%;
  min-height: calc(100vh - 59px);
  padding: 20px;
}
